<template>
    <div class="share" >
        <turn-table
            ref="turntable"
            v-model="luckyId"
            :awards="shareAwards"
            @start="start"
            @stop="stop"/>
        <div
            class="btn"
            @click="start"/>
        <div class="list-title">获奖名单</div>
        <van-swipe
            class="swipe"
            :autoplay="3000"
            :show-indicators="false"
            :vertical="true">
            <van-swipe-item
                class="swipe-item"
                v-for="(item, i) in swipeLIst"
                :key="i">
                {{ item }}
            </van-swipe-item>
        </van-swipe>
        <gray-mask :show="dialogKey">
            <lottery-dialog
                :icon="dialog.icon"
                :title="dialog.title"
                :description="dialog.description"
                :button-text="dialog.buttonText"
                @btn-click="dialogClickHandler" />
        </gray-mask>
    </div>
</template>

<script>
import GrayMask from '@/components/GrayMask';
import { goApp } from '@/utils/share';
import TurnTable from './turntable';
import LotteryDialog from './Dialog';
import { swipeLIst, dialogMap, shareAwards } from './config';

export default {
    name: 'Share',
    components: {
        TurnTable,
        LotteryDialog,
        GrayMask,
    },
    data() {
        return {
            luckyId: NaN,
            shareAwards,
            swipeLIst,
            dialogMap,
            dialogKey: '',
            canStart: true,
        };
    },
    computed: {
        dialog() {
            return this.dialogMap[this.dialogKey] || {};
        },
    },
    mounted() {
        this.$root.hidePlaceholder();
        this.$o2o({
            e_c: '大转盘',
            e_a: '曝光',
            e_n: '端外-首页',
        });
    },
    methods: {
        start() {
            if (!this.canStart) {
                return;
            }
            this.canStart = false;
            this.$refs.turntable.start();
            this.$o2o({
                e_c: '大转盘',
                e_a: '点击',
                e_n: '端外-开始抽奖',
            });
            setTimeout(() => {
                const random = Math.random();
                switch (true) {
                case random < 0.5:
                    this.luckyId = 2;
                    break;
                case random < 0.76:
                    this.luckyId = 1;
                    break;
                case random < 0.86:
                    this.luckyId = 4;
                    break;
                case random < 0.96:
                    this.luckyId = 3;
                    break;
                default:
                    this.luckyId = 5;
                    break;
                }
            }, 2000);
        },
        openDialog(dialogKey) {
            if (dialogKey) {
                this.$o2o({
                    e_c: '大转盘',
                    e_a: '曝光',
                    e_n: '端外-中奖弹窗',
                });
            }
            this.dialogKey = dialogKey;
        },
        stop() {
            this.openDialog(shareAwards.filter(v => v.choiceId === this.luckyId)[0].dialog);
            this.canStart = true;
        },
        // 弹窗中的按钮点击
        dialogClickHandler() {
            if (this.dialogKey !== 'badluck') {
                this.$o2o({
                    e_c: '大转盘',
                    e_a: '点击',
                    e_n: '端外-去想看领取',
                }, () => {
                    goApp();
                }, () => {
                    goApp();
                });
            }
            this.openDialog('');
        },
    },
};
</script>


